import {Component, Input, OnChanges, OnInit, SimpleChanges} from '@angular/core';
import * as moment from "moment";
import {ChatInterface} from "../../utils/messages";
import ChartChart = ChatInterface.ChartChart;
import ChartValue = ChatInterface.ChartValue;

@Component({
  selector: 'app-bar-graph',
  templateUrl: './bar-graph.component.html',
  styleUrls: ['./bar-graph.component.scss']
})
export class BarGraphComponent implements OnChanges {
  @Input() data: ChartChart;
  bar: ChartValue[];

  customColors = (value)=>{
    return "#69b2f8";
  }

  monthes = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'];

  xAxisTickFormatting = (value: Date) => {
    const date = moment(value, 'L');
    const month = date.month();
    const res = this.monthes[month];
    // console.log(res);
    return res;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.data && this.data.series && this.data.series.length)
      this.bar = this.data.series[0].series;
  }
}
