import {Component, Input, OnChanges, OnInit} from '@angular/core';
import {Series} from "@swimlane/ngx-charts/lib/models/chart-data.model";
import {ChatInterface} from "../../utils/messages";
import ChartChart = ChatInterface.ChartChart;
import ChartSerie = ChatInterface.ChartSerie;

@Component({
  selector: 'app-multi-values-graph',
  templateUrl: './multi-values-graph.component.html',
  styleUrls: ['./multi-values-graph.component.scss']
})
export class MultiValuesGraphComponent implements OnChanges {

  @Input() data: ChartChart;
  series: ChartSerie[] = [];

  // options
  animations: boolean = true;
  xAxis: boolean = true;
  showXAxisLabel: boolean = true;
  timeline: boolean = true;

  customColor = (value) => {
    // console.log(value);
    if (this.data) {
      if (value == this.data.series[0].name)
        return '#EEE';
      if (value == this.data.series[1].name)
        return '#d1e6fa';
      if (value == this.data.series[2].name)
        return '#69b2f8';
    }
  }

  ngOnChanges(): void {
    if (this.data && this.data.series)
      this.series = this.data.series;
  }

}
